.footer-nav{
	position: fixed;
	bottom: 0;
	width: 100%;
	left: 0;
	background-color: #f1f1f1;
	@include display-flex();
	z-index: 120;
	height: .85rem;

	li{
		@include flex(1);
		height: 100%;
		position: relative;

		a{
			display: block;
			width: 100%;
			height: 100%;
		}

		img{
			position: absolute;
			height: 100%;
			left: 50%;
			top: 50%;
			@include translate(-50%, -50%);
		}

		&.active a{
			color: #c81522;
		}
	}
}